import React from 'react'
import { Img, CardStyle } from './CardStyles';


const HomeCard = (props) => {

    const {image, Type, Description} = props.home

    return (
        <CardStyle>
            <Img src={image} alt=""/>
            <article>
                {Type}
                {Description}
            </article>
        </CardStyle>
    )
}

export default HomeCard
    