import React from 'react'
import HomeInfo from './HomeInfo'
import HomeCard from './HomeCard'
import { MainStyle } from './CardStyles'

const HomeList = () => {
    return (
        <MainStyle>
            {HomeInfo.map((home) => {
                return <HomeCard key={home.id} home={home}></HomeCard>
            })}
        </MainStyle>
    )
}

export default HomeList
